import { atomWithStorage } from "jotai/utils";

interface MigrationFns<T> {
  onLoad?: (value: any) => T;
  onSet?: (value: T) => any;
}

export function atomWithChromeStorage<T>(
  key: string,
  initialValue: T,
  migrationFns?: MigrationFns<T>
) {
  return atomWithStorage<T>(key, initialValue, {
    getItem: async (key: string) => {
      const result = await chrome.storage.local.get(key);
      if (result[key] === undefined) {
        return initialValue;
      }
      if (migrationFns?.onLoad) {
        return migrationFns.onLoad(result[key]);
      }
      return result[key];
    },
    setItem: async (key: string, value: T) => {
      if (migrationFns?.onSet) {
        value = migrationFns.onSet(value);
      }
      await chrome.storage.local.set({ [key]: value });
    },
    removeItem: async (key: string) => {
      await chrome.storage.local.remove(key);
    },
  });
}
