"use client";
import "@radix-ui/themes/styles.css";
import { Theme } from "@radix-ui/themes";
import { createContext, ReactNode, useContext } from "react";
import { useAppearance } from "../hooks/useAppearance";

interface AppThemeProps {
  children: ReactNode;
  appearance?: "light" | "dark" | "auto";
}

const ThemeValueProvider = createContext<"light" | "dark">("light");

export function AppTheme({
  children,
  appearance: appearanceProp = "auto",
}: AppThemeProps) {
  const { appearance } = useAppearance();
  const theme = appearanceProp === "auto" ? appearance : appearanceProp;

  return (
    <ThemeValueProvider.Provider value={theme}>
      <Theme
        accentColor="plum"
        radius="full"
        appearance={theme}
        panelBackground="translucent"
      >
        {children}
      </Theme>
    </ThemeValueProvider.Provider>
  );
}

export function useThemeValue() {
  return useContext(ThemeValueProvider);
}
